.size {
    max-width: 540px
}
.card {
    background: #fff;
    border-radius: 2px;
    height: 345px;
    margin: 1rem;
    position: relative;
    width: 325px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    margin-left: 10px;
  }
  
  .card > .img-container {
    height: 50%;
    overflow: hidden;
    text-align: center;
    background:#6CADDC;
  }
  .img-size {
    width: 100%;
    height: 200px
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
  }
  
  .card > .content > ul {
    list-style-type: none;
  }
  
  .card > .content > ul > li {
    margin-top: 18px;
  }